<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process214"></a>

<p><br></p>
<p><span class="rvts17">精灵先知（</span><span class="rvts50">The Elven Seer</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts63"><br></span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:146,Y:760</span><span class="rvts21">，锯木厂二楼</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">解救萨黑拉并逃往精灵营地</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1275.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">该任务只能在成功进入精灵营地后并完成埋葬仪式后获得，然而，即便不获得该任务支线，解救萨黑拉并使她成功到达精灵营地，也会使该任务瞬间领取并以好的结局完成。</span>
<br><span class="rvts21">如果此时队伍中有塞碧莉，那么她将会亮明她的真实身份，并要求成为核心人物。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1276.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">萨黑拉在成功逃离欢乐堡后，被孤狼首领罗斯特（</span><span class="rvts20">Roost</span><span class="rvts21">）掳走，成功解救萨黑拉回到精灵营地，她会教你掌控源力，增加</span><span class="rvts20">1</span><span class="rvts21">点最大源力值。</span>
<br><span class="rvts21">锯木厂里面充满了孤狼士兵，如果想要完成该区域的其它任务，请务必平静进入锯木厂。无论是先杀掉罗斯特，还是先与锯木厂士兵发生战斗，都会使锯木厂所有的孤狼士兵或长官变得敌对。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



